html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  z-index: 1;
}

body._lock {
  overflow: hidden;
  background-color: #e1e6e4;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  position: relative;
  overflow: hidden;
  max-width: 1440px;
  margin: 0 auto;
  z-index: 1000;
}

.wrapper {
  background-color: #fff;
  z-index: 999;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1188px;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
}

@media (max-width: 1182px) {
  .container {
    max-width: 1000px;
  }
}

@media (max-width: 991.98px) {
  .container {
    max-width: 900px;
  }
}

@media (max-width: 767.98px) {
  .container {
    max-width: none;
    padding: 0 10px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

._tabs-block {
  display: none;
}

._tabs-block._active {
  display: block;
}

.section {
  min-height: 100vh;
}

.section__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 70px;
}

@media (max-width: 767.98px) {
  .section__header {
    flex-direction: column;
  }
}

.section__header--partners {
  flex-direction: column;
  align-items: start;
  margin-bottom: 0;
}

@media (max-width: 767.98px) {
  .section__header--partners {
    width: 100%;
  }
}

.section__header--partners .section__title {
  margin-bottom: 15px;
}

.section__title {
  font-family: "Play";
  font-weight: 700;
  font-size: 48px;
  line-height: 130%;
  letter-spacing: 0.005em;
  color: #006464;
}

@media (max-width: 1182px) {
  .section__title {
    font-size: 38px;
    line-height: 120%;
  }
}

@media (max-width: 767.98px) {
  .section__title {
    margin-bottom: 10px;
    font-size: 32px;
  }
}

.section__description {
  font-size: 20px;
  line-height: 120%;
  letter-spacing: 0.045em;
  color: #006464;
  flex: 0 0 59%;
}

@media (max-width: 1182px) {
  .section__description {
    font-size: 18px;
  }
}

@media (max-width: 767.98px) {
  .section__description {
    font-size: 16px;
  }
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 300px;
}

body {
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "Roboto";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.select {
  position: relative;
}

.select__item {
  position: relative;
}

.select__title {
  cursor: pointer;
  color: #000000;
  border: none;
  background-color: transparent;
  border-radius: 4px;
}

@media (max-width: 991.98px) {
  .select__title {
    font-size: 18px;
  }
}

.select__value {
  color: #006464;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row-reverse;
  font-size: 16px;
  height: 30px;
  padding: 0 15px;
  font-weight: 500;
}

.select__value span {
  overflow: hidden;
}

.select__value:before {
  content: "";
  display: inline-block;
  transition: all 0.3s ease;
  width: 6px;
  height: 4px;
  background: url("../img/arrow.png") center/100% no-repeat;
  margin-left: 4px;
}

.select__options {
  display: none;
  position: absolute;
  left: 0;
  min-width: 100%;
  z-index: 100;
  top: 28px;
  font-size: 14px;
  padding: 4px 0;
  border-radius: 4px;
  background-color: #ffffff;
  border: 1px solid #d9d9d9;
  color: #000000;
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.2);
}

@media (max-width: 991.98px) {
  .select {
    font-size: 18px;
  }
}

.select__option {
  cursor: pointer;
  padding: 5px 15px;
  margin: 0;
}

.select__option:hover {
  background-color: rgba(0, 100, 100, 0.6);
  color: #ffffff;
}

.select._active {
  z-index: 5;
}

.select._active .select__value:before {
  transform: rotate(-180deg);
}

.select._active .select__options {
  display: block;
}

.header {
  position: fixed;
  width: 100%;
  max-width: 1440px;
  right: 0;
  left: 0;
  top: 0;
  z-index: 101;
  max-width: inherit;
  margin: 0 auto;
}

.header:after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 2px;
  width: 100%;
  background-color: #fff;
}

.header__wrapper {
  width: 100%;
  position: relative;
  max-width: 1440px;
  margin: 0 auto;
}

.header._scroll {
  position: fixed;
  margin: 0 auto;
  background-color: #e1e6e4;
  z-index: 1000;
  max-width: inherit;
  left: 0;
  right: 0;
}

.header._scroll .header__container {
  height: 90px;
}

.header__container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 90px;
  padding: 10px 15px;
  background-color: #e1e6e4;
}

@media (max-width: 767.98px) {
  .header__container {
    height: 56px;
  }
}

.header__burger {
  display: none;
  position: relative;
  width: 24px;
  height: 16px;
}

@media (max-width: 767.98px) {
  .header__burger {
    display: block;
  }
}

.header__phone {
  display: flex;
  align-items: center;
}

.header__phone-mobile {
  display: none;
}

@media (max-width: 1182px) {
  .header__phone-mobile {
    display: block;
  }
}

.header__phone-link {
  position: relative;
  display: block;
  color: #00a050;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  letter-spacing: 0.045em;
  padding-left: 48px;
}

@media (max-width: 1182px) {
  .header__phone-link {
    display: none;
  }
}

.header__phone-link::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-image: url(../../img/whatsap.png);
  width: 32px;
  height: 32px;
}

@media (max-width: 767.98px) {
  .header__language {
    display: none;
  }
}

.logo {
  display: block;
}

@media (max-width: 479.98px) {
  .logo svg {
    width: 115px;
  }
}

.logo svg {
  width: 153px;
  height: 24px;
}

.navbar {
  padding: 0 10px;
}

@media (max-width: 767.98px) {
  .navbar {
    display: none;
  }
}

.menu {
  display: flex;
}

.menu__item {
  margin-right: 48px;
}

@media (max-width: 991.98px) {
  .menu__item {
    margin-right: 20px;
  }
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  position: relative;
  display: inline-block;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.045em;
  color: #006464;
  padding: 10px 0;
  overflow: hidden;
}

.menu__link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #f1b841;
  opacity: 0;
  transition: 0.3s linear;
  transform: translateX(-100%);
}

.menu__link:hover::before, .menu__link.is-active::before {
  opacity: 1;
  transform: translateX(0);
}

.icon-menu {
  display: none;
}

@media (max-width: 767.98px) {
  .icon-menu {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 18px;
    cursor: pointer;
    z-index: 5;
  }
  .icon-menu span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0px;
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #006464;
  }
  .icon-menu span:first-child {
    top: 0px;
  }
  .icon-menu span:last-child {
    top: auto;
    bottom: 0px;
  }
  .icon-menu span:nth-child(2) {
    width: 16px;
  }
  .icon-menu._active span {
    transform: scale(0);
  }
  .icon-menu._active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 2px);
    width: 18px;
  }
  .icon-menu._active span:last-child {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
    width: 18px;
  }
}

.menu__body {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-200%);
  opacity: 0;
  transition: 0.5s ease-in-out;
  z-index: 100;
}

.menu__body._active {
  position: fixed;
  transform: translateY(0);
  opacity: 1;
  display: block;
  width: 100%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #e1e6e4;
  z-index: 100;
  padding-top: 96px;
}

.menu__body._active .menu {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mobil {
  padding: 40px;
  padding-right: 0;
}

.mobil__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.mobil__header .header__phone-link,
.mobil__header .header__language {
  display: block;
}

.mobil__header .header__phone-mobile {
  display: none;
}

.mobil__menu-item {
  border-bottom: 1px solid #f2f2f2;
}

.mobil__menu-link {
  display: block;
  font-size: 24px;
  line-height: 120%;
  letter-spacing: 0.045em;
  color: #006464;
  padding: 18px 0;
}

.mobil__footer {
  position: absolute;
  bottom: 32px;
  left: 50%;
  transform: translateX(-50%);
}

.mobil__footer .footer__copyright {
  margin-top: 26px;
}

.footer {
  background: #003b3b;
  padding: 80px 0;
}

@media (max-width: 991.98px) {
  .footer {
    padding: 40px 0 20px;
  }
}

.footer__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 991.98px) {
  .footer__row {
    flex-direction: column;
    align-items: start;
  }
}

.footer__col {
  border-right: 2px solid #003131;
  padding: 0 40px;
  min-height: 72px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 991.98px) {
  .footer__col {
    border-right: 0;
    flex: 0 0 100% !important;
    padding: 0;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: start;
  }
}

.footer__col:last-child {
  display: none;
  border: none;
  padding-right: 0;
}

@media (max-width: 991.98px) {
  .footer__col:last-child {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}

.footer__col:first-child {
  padding-left: 0;
}

.footer__col:nth-child(2) {
  flex: 0 0 30%;
}

@media (max-width: 767.98px) {
  .footer__col:nth-child(2) {
    border-bottom: 2px solid #003131;
    justify-content: center;
    align-items: start;
  }
}

@media (max-width: 479.98px) {
  .footer__col:nth-child(2) {
    max-width: 300px;
  }
}

.footer__col:nth-child(3) {
  flex: 0 0 28%;
}

@media (max-width: 767.98px) {
  .footer__col:nth-child(3) {
    width: 100%;
  }
}

.footer__col:nth-child(4) {
  flex: 0 0 28%;
}

@media (max-width: 767.98px) {
  .footer__col:nth-child(4) {
    border-bottom: 2px solid #003131;
    width: 100%;
  }
}

.footer__text {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.4px;
  color: #028585;
}

.footer__link {
  font-weight: bold;
  font-size: 24px;
  line-height: 130%;
  letter-spacing: 0.005em;
  color: #028585;
}

@media (max-width: 1182px) {
  .footer__link {
    font-size: 20px;
  }
}

@media (max-width: 767.98px) {
  .footer__link {
    font-size: 16px;
  }
}

.footer__copyright, .footer__politic,
.footer__politic a {
  font-size: 11px;
  line-height: 16px;
  letter-spacing: 0.4px;
  color: #028585;
  text-align: center;
}

.footer-block {
  margin-top: 24px;
}

@media (max-width: 991.98px) {
  .footer-block {
    display: none;
  }
}

.footer-copy {
  display: flex;
  flex-direction: column;
}

.footer-copy a {
  display: block;
}

@media (max-width: 991.98px) {
  .footer--mobile .footer__copyright,
  .footer--mobile .footer__politic {
    display: block;
  }
}

.social {
  display: flex;
